<template>
  <div class="bottom-bar">
    <div class="check-content">
      <check-button class="check-button" :isChecked="isSelectAll" @click.native="selectAll"></check-button>
      <span>全选</span>
    </div>

    <div class="total-price">总计： {{totalPrice}}</div>

    <div class="total-count">去结账({{totalCount}})</div>
  </div>
</template>

<script>
import checkButton from "./checkButton";
export default {
  name: "bottomBar",
  components: {
    checkButton
  },
  computed: {
    totalPrice() {
      return (
        "￥" +
        this.$store.state.cartList
          .filter(item => {
            return item.checked;
          })
          .reduce((preValue, item) => {
            return preValue + item.count * item.countprice;
          }, 0)
      );
    },
    totalCount() {
      return this.$store.state.cartList.filter(item => {
        return item.checked;
      }).length;
    },
    isSelectAll() {
      if (!this.$store.state.cartList.length) return false;
      return !this.$store.state.cartList.find(item => {
        return !item.checked;
      });
    }
  },
  methods: {
    selectAll() {
      if (this.isSelectAll) {
        return this.$store.state.cartList.forEach(element => {
          return (element.checked = false);
        });
      } else {
        return this.$store.state.cartList.forEach(element => {
          return (element.checked = true);
        });
      }
    }
  }
};
</script>

<style scoped>
.bottom-bar {
  text-align: center;
  width: 100%;
  height: 50px;
  line-height: 50px;
  display: flex;
  position: relative;
  top: -50px;
  background-color: #fff;
  border-top: 1px solid #ddd;
  /* padding: 0 5px; */
}

.check-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 70px;
  /* height: 40px; */
  /* line-height: 40px; */
}

.check-button {
  height: 20px;
  line-height: 20px;
}
.check-content span {
  /* margin-left: 8px ; */
}

.total-price {
  flex: 1;
}

.total-count {
  background-color: red;
  width: 80px;
}
</style>