      <cart-list-item v-for="(item,index) in list" :key="index" :cartlistitem="item"></cart-list-item>
    </scroll>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import Scroll from "components/common/betterscroll/BetterScroll";
import CartListItem from "./CartListItem";

export default {
  name: "CartList",
  components: {
    CartListItem,
    Scroll
  },
  computed: {
    ...mapGetters({
      list: "cartList"
    })
  }
};
</script>

